}

.mobile-apps .image-upload-div img {
  max-height: initial;
  position: absolute;
  top: 0;
  left: 0;
}

.mobile-apps .Upload .file-label {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  height: initial;
  width: initial;
  background-color: #fbfcfc;
}

.mobile-apps textarea {
  height: 100px;
  resize: none;
}

.mobile-apps .scroller {
  white-space: nowrap;
  overflow: auto;
  margin-bottom: 40px;
}

.mobile-apps .theme-option {
  display: inline-block;
  margin-bottom: 20px;
}

.mobile-apps .theme-option:not(:last-child) {
  margin-right: 30px;
}

.mobile-apps .theme-option label {
  cursor: pointer;
}

.mobile-apps .theme-name {
  font-weight: 600;
  margin-top: 10px;
  font-size: 0.875rem;
}

.mobile-apps .theme-preview-container {
  border-radius: 2px;
  border: 1px solid #eaefef;
  padding: 10px;
  position: relative;
}

.mobile-apps .theme-option.selected .theme-preview-container {
  border-color: #20c05c;
}

.mobile-apps .theme-preview-container::before {
  position: absolute;
  content: '';
  display: block;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
}

.mobile-apps .theme-option.selected .theme-preview-container::before {
  background-color: rgba(0, 0, 0, 0.5);
}

.mobile-apps .theme-option.selected .theme-preview-container::after {
  height: 40px;
  width: 40px;
  content: '';
  display: block;
  background-color: #20c05c;
  border-radius: 50%;
  background-image: url(./tick.svg);
  background-position: center;
  background-size: 24px;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.mobile-apps .theme-preview-container img {
  height: 380px;
}

.mobile-apps .invisible {
  height: 0;
  width: 0;
  overflow: hidden;
  visibility: hidden;
  display: block;
}

