      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.mobile-apps .loader-message {
  font-size: 0.75rem;
  margin-bottom: 20px;
}

.mobile-apps .Tooltip {
  position: relative;
  display: inline-block;
  top: -6px;
}

.mobile-apps .Tooltip .tooltip-text {
  display: none;
  position: absolute;
  z-index: 1;
  width: 220px;
  background-color: #2b3238;
  color: #fff;
  text-align: left;
  padding: 10px;
  border-radius: 6px;
  font-size: 0.75rem;
  top: 100%;
  right: -4px;
  margin-top: 4px;
}

.mobile-apps .Tooltip .tooltip-text::before {
  content: ' ';
  position: absolute;
  bottom: 100%;
  right: 8px;
  margin-left: -6px;
  border-width: 6px;
  border-style: solid;
  border-color: transparent transparent #2b3238 transparent;
}

.mobile-apps .Tooltip:hover .tooltip-text {
  display: block;
}

@media screen and (min-width:551px) {
  .mobile-apps .form-fields {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    margin: 0 -15px;
  }
  
  .mobile-apps .field {
    width: 50%;
    padding: 0 15px;
  }
  
  .mobile-apps .app-name {
    margin-right: 50%;
  }
}


