.StyledSelect {
  position: relative;
  width: 100%;
}

.StyledSelect .container {
  border: 1px solid var(--border-color);
  background-color: white;
  padding: 10px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.StyledSelect .dropdown {
  position: absolute;
  top: 100%;
  border: 1px solid var(--border-color);
  border-top: none;
  background-color: white;
  width: 100%;
  padding: 5px 0;
}

.StyledSelect .container::after {
  width: 20px;
  height: 10px;
  background-image: url(./arrow.svg);
  content: '';
  -ms-flex-item-align: center;
      align-self: center;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-transition: -webkit-transform ease-in-out 0.1s;
  transition: -webkit-transform ease-in-out 0.1s;
  -o-transition: transform ease-in-out 0.1s;
  transition: transform ease-in-out 0.1s;
  transition: transform ease-in-out 0.1s, -webkit-transform ease-in-out 0.1s;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
}

.StyledSelect.expanded .container::after {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

.StyledSelect .dropdown-item {
  padding: 5px 10px;
  cursor: pointer;
  position: relative;
}

.StyledSelect .dropdown-item:hover::before {
  background-color: #20c05c;
  display: block;
  content: '';
  width: 2px;
  height: 100%;
  position: absolute;
  left: -2px;
  top: 0;
}


