.campaign-form .variables {
    display: inline-block;
    margin-right: 10px;
    padding: 5px 10px;
    font-size: 14px;
    margin-bottom: 40px;
    color: #fff;
    background-color: #337ab7;
    cursor: pointer;
}

.campaign-form {
    max-width: inherit;
}

.campaign-form .campaign-form-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2%;
}

.campaign-form .flex.date-time-wrapper {
    margin-top: -2px;
}

.campaign-form .campaign-time Select {
    height: 46px;
    font-weight: 600;
}

.campaign-form .campaign-time {
    margin-right: 10px;
    margin-bottom: 0;
}

.campaign-form .time-meridian {
    margin-top: 16px;
    margin-bottom: 0;
}

.campaign-form .time-meridian .radio-label {
    min-width: 50px;
    height: 46px;
    margin-bottom: 0;
    border: 0;
    padding: 5px;
}

.campaign-form .sms {
    margin-bottom: 5px;
}

.campaign-form .comment-box {
    margin-top: 20px;
    width: 49%;
}

.campaign-form .comment-box textarea, .campaign-form .sms textarea {
    height: 80px;
}

.campaign-form .date-picker {
    max-width: 14rem;
    margin-right: 10px;
    margin-bottom: 0;
}

.campaign-form .body {
    max-width: 32rem;
}

.campaign-form .tab-container {
    margin-bottom: 20px;
    margin-right: 30px;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.campaign-form .previewer {
    border: 1px solid #e7eaec;
    padding: 15px;
    position: relative;
    min-height: 60px;
}

.campaign-form .tab-container {
    max-width: 60%
}

.campaign-form .sms-content {
    font-size: 0.8125rem;
}

.campaign-form .previewer .email-subject {
    padding: 10px 5px;
    margin: 0 -5px 10px;
    font-size: 0.8125rem;
}

.campaign-form .previewer .email-body {
    padding: 5px 20px;
    background-color: #fafafa;
    font-size: 0.8125rem;
    font-family: sans-serif;
    line-height: 20px;
    color: #000;
}

.campaign-form .previewer .email-body pre {
    white-space: pre-wrap;
    overflow-y: auto;
}

.campaign-form .preview-wrapper {
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 20%;
}

.campaign-form .preveiw-heading {
    font-weight: 400;
    border-bottom: 1px solid #E5E6E7;
    padding: 5px 10px;
    margin-bottom: 15px;
}

.campaign-form .email-container, .campaign-form .sms-container {
    padding: 10px 18px;
    border: solid 1px #eaefef;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    margin-top: -1px;
}

.campaign-form .invalid-date-time {
    margin-top: 10px;
}

@media (max-width: 800px) {
    .campaign-form .tabs-preview-wrapper {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .campaign-form .preview-wrapper {
        width: 100%;
        margin-bottom: 20px;
    }
    .campaign-form .tab-container {
        max-width: 100%;
    }
    .campaign-form .date-picker {
        margin-bottom: 20px;
    }
}

@media (max-width: 768px) {
    .campaign-form .campaign-form-container{
      grid-template-columns: 1fr 2fr;
    }
    .campaign-form .comment-box {
        margin-top: 0px;
        width: 62%;
    }
    .campaign-form .body {
        max-width: 100%;
    }
}

@media (max-width: 550px) {
    .campaign-form .date-time-wrapper {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .campaign-form .campaign-form-container {
        grid-template-columns: 1fr;
    }
    .campaign-form .date-picker {
        max-width: none;
        width: 100%;
    }
    .campaign-form .date-picker {
        margin-bottom: 10px;
    }
    .campaign-form .comment-box {
        width: 100%;
        margin-top: 10px;
    }
    .campaign-form .tab-container {
        margin-right: 0px;
    }
    .campaign-form .flex.time-wrapper {
        margin-bottom: 15px;
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .campaign-form .time-meridian .radio-label {
        z-index: 0;
    }

    .campaign-form .form-buttons-container {
        display: -ms-flexbox;
        display: flex;
    }
    .campaign-form .form-buttons-container .primary, .campaign-form .form-buttons-container .button{
        -ms-flex: 1 1;
            flex: 1 1;
    }
}



