.vehicle-vendor-page .vehicle-btn {
    margin-right: 10px;
}

@media screen and (max-width: 480px){
    .vehicle-vendor-page .primary.button.vehicle-btn, .vehicle-vendor-page .header-actions-wrapper .button span.text {
        font-size: 14px;
    }
    .vehicle-vendor-page .header-actions-wrapper .button span.text {
        display: inline;
    }
    .vehicle-vendor-page .header-actions-wrapper a {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    .vehicle-vendor-page .header-container {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .vehicle-vendor-page .header-actions-wrapper .search-button-wrapper {
        position: absolute;
        top: 0px;
        right: -15px;
    }
    .vehicle-vendor-page .header-actions-wrapper {
        margin-left: -88px;
    }
    .vehicle-vendor-page .header-actions-wrapper input[type="submit"],.vehicle-vendor-page .header-actions-wrapper .button {
        padding: 0px 5px;
    }
    .vehicle-vendor-page .header-actions-wrapper .primary, .vehicle-vendor-page .header-actions-wrapper .button {
        min-width: 48%;
    }
}
@media screen and (max-width: 360px){
    .vehicle-vendor-page .header-actions-wrapper {
        margin-left: -74px;
    }
}


