.trip-payments-form {
  max-width: initial;
}
.trip-payments-form .total-amount {
  font-weight: 600;
}
.trip-payments-form .payment-input input{
  max-width: 160px;
}
.trip-payments-form .trx-id {
  min-height: 20px;
  padding-left: 10px
}
.trip-payments-form .labelWrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 200px;
  -ms-flex-align: start;
      align-items: flex-start;
}

.trip-payments-form .labelWrap label {
  line-height: 44px;
  margin: 0;
}

.trip-payments-form .field {
  display: grid;
  grid-template-columns:  repeat(3,1fr);
  grid-gap: 20px;
  margin-bottom: 12px;
}

.trip-payments-form .field input[type=number] {
  width: 248px;
}

.trip-payments-form .pending-amount {
  color: #eb8181;
  height: 44px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.875rem;
}

.trip-payments-form h5 {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
}

.trip-payments-form .table-cell.payment-method {
  width: 160px;
}

.trip-payments-form .table-cell.payment-method .text-muted {
  line-height: 40px;
  padding-left: 10px;
}

.trip-payments-form .table .table-cell:last-child {
  text-align: left;
}

.trip-payments-form .form-actions {
  margin-top: 40px;
  -ms-flex-pack: left;
      justify-content: left;
}

.trip-payments-form form {
  max-width: 100%;
}

.trip-payments-form .table {
  display: block;
  overflow-x: auto;
 }
 
@media screen and (max-width: 768px) {
  .trip-payments-form .field {
    display: inherit;
  }
}


