.invoice-popup {
  max-width: 1140px;
}

.invoice-popup .header {
  background-color: transparent;
}

.invoice-popup .close {
  right: 0;
  color: white;
  border: none;
  background-image: url(../close-icon.svg);
  background-size: 100%;
  cursor: pointer;
}

.invoice-popup .details {
  padding: 0;
}

.invoice-popup .printable-area {
  padding: 25px;
}

h2 {
  margin: 0;
  padding: 0;
}

.invoice-popup .invoice-header {
  padding-top:1.35rem;
  margin-bottom: 18px;
  text-align: right;
}

.invoice-popup .section-title-slot-title {
  font-size: 0.75rem;
  color: #80959d;
  margin: 0 0 20px 0;
  line-height: 28px;
  margin-bottom: 0;
}

.invoice-popup .organization-name {
  color: #2b3238;
  font-weight: 600;
  font-size: 0.875rem;
  letter-spacing: 0.1px;
  font-weight: 600;
  margin-bottom: 0px;
  margin-top: 6px;

}

.invoice-popup .customer-addres {
  font-size: 0.75rem;
  color: #80959d;
}

.invoice-popup .table .table-header > .table-cell {
  border: none;
  height: 38px;
}

.invoice-popup .table .table-row > .table-cell {
  height: 38px;
}

.invoice-popup .table-cell:first-child {
  display: none;
}

.invoice-popup .table-cell:nth-child(2) {
  padding-left: 20px;
}

.invoice-popup .store-address-wrapper {
  margin-bottom: 20px;
}

.invoice-popup .order-number {
  margin-bottom: 2px;
  color: #2b3238 !important;
  cursor: default !important;
}

.invoice-popup .store-address,
.invoice-popup .order-time {
  font-size: 0.75rem;
  color: #80959d;
}

.invoice-popup .customer-address-wrapper {
  width: 40%;
}

.invoice-popup h2 {
  margin: 0 0 18px 0;
}

.invoice-popup h3 {
  margin: 0 0 10px 0;
}

.invoice-popup .section-title {
  display: none;
}

.invoice-popup .customer-name,
.invoice-popup .store-name,
.invoice-popup .reference-number {
  color: #2b3238;
  font-weight: 600;
  font-size: 0.875rem;
  letter-spacing: 0.1px;
  font-weight: 600;
  margin-bottom: 4px;
}

.invoice-popup .customer-phone {
  font-size: 0.75rem;
}

.invoice-popup .order-items-view {
  margin: 12px 0;
  text-align: left;
}
/* 
.invoice-popup .table-footer {
  display: none;
} */

.invoice-popup .tax-disclaimer {
  font-size: 0.625rem;
  text-align: center;
}

.invoice-popup .thank-you {
  font-size: 0.75rem;
  margin: 5px 0;
}

.invoice-popup .invoice-summary .total-amount,
.invoice-popup .invoice-summary .payment-amount {
  font-size: 0.875rem;
}

.invoice-popup .invoice-summary .total-amount {
  margin-bottom: 8px;
}

.invoice-popup .invoice-summary .payment-amount {
  margin-top: 8px;
}

.invoice-popup .invoice-summary .order-amount,
.invoice-popup .invoice-summary .shipping-charge,
.invoice-popup .invoice-summary .discount-amount,
.invoice-popup .invoice-summary .tax-amount {
  font-size: 0.75rem;
  line-height: 20px;
}

.invoice-popup .entity-key {
  font-size: 0.75rem;
}

.invoice-popup .order-items-view .brand-name {
  font-weight: normal;
}
.printable-area .org-logo img {
  max-width: 200px;
  max-height: 100px;
}
.printable-area .org-logo {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.printable-area .store-address-wrapper {
  text-align: right;
}
@media print {

  .modal-backdrop{
    position: absolute !important;
    top:0px;
    left: 0px;
    overflow: visible !important;  
  }
  .table .table-cell:not(:first-child) {
    display: table-cell !important;
  }

  main {
    display: none;
  }

  @page {
    size: auto;
    margin:1.5cm 5.5mm;
  }
  html {
    font-size: 12pt;
  }
  body {
    margin: 1.6cm;
    /* font-family: monospace !important; */
  }
  body * {
    visibility: hidden;
  }
  .invoice-popup {
    top: 0;
  }

  .printable-area,
  .printable-area * {
    /* overflow-y: scroll; */
    visibility: visible !important;
    background: transparent !important;
  }
  .printable-area {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 15mm 15mm !important;
  }
  .printable-area .table-header > .table-cell {
    color: #2b3238;
  }
  .printable-area .table-cell {
    padding-top: 6px;
    padding-bottom: 6px;
  }
  .printable-area .table-cell:nth-child(2) {
    padding-left: 0;
  }
  .printable-area .table-cell:last-child {
    padding-right: 0;
  }
  .printable-area .invoice-summary {
    padding: 0;
    width: 40%;
  }
  .printable-area .text-muted,
  .printable-area .store-address,
  .printable-area .table-cell,
  .printable-area .order-time {
    color: #808080;
  }
  .printable-area .table-row > .item-amount {
    font-weight: normal;
  }
}

@media print and (max-width: 42em) {
  /* Thermal print sheet design */
  html {
    font-size: 8pt;
  }
  body * {
    color: black !important;
    margin: 0cm;
  }
  .printable-area {
    padding: 4mm 4mm 1cm 4mm !important;
  }
  .printable-area .invoice-header {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .printable-area .order-number {
    font-size: 1rem;
  }
  .printable-area .order-addresses {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .printable-area .customer-address-wrapper,
  .printable-area .store-address-wrapper {
    margin: 0;
    width: 100%;
  }
  .printable-area .customer-address-wrapper {
    margin-bottom: 12px;
  }
  .printable-area .invoice-footer {
    -ms-flex-flow: column-reverse;
        flex-flow: column-reverse;
  }
  .printable-area .invoice-summary {
    padding: 0;
    width: auto;
  }
  .printable-area .tax-disclaimer {
    margin-top: 16px;
  }
  /* .printable-area .item-quantity {
    width: 10px;
  } */
  .printable-area .item-mrp,
  .printable-area .item-discount {
    display: none;
  }
  .printable-area .item-amount {
    white-space: nowrap;
  }
  .printable-area .table-header > .item-quantity {
    font-size: .75rem;
  }
  /* .printable-area .table-header > .item-quantity::after {
    font-size: 0.75rem;
    content: attr(data-abbreviation);
  }  */
  .invoice-popup .table-cell {
    padding-top: 4pt;
    padding-bottom: 4pt;
    border-color: #aaa !important;
  }
}

@media screen and (max-width: 440px) {
  .invoice-popup .table .table-cell {
    padding: 10px 1vw;
  }
} 



