.loader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: auto;
  padding: 14px;
}

.loader-spinner {
  margin: 0 auto;
  display: block;
  fill: transparent;
  stroke: #009688;
}

.loader-sm {
  stroke-width: 2;
  -webkit-animation: dash-sm 2s ease infinite,rotate 2s linear infinite;
          animation: dash-sm 2s ease infinite,rotate 2s linear infinite;
}
.loader-md {
  -webkit-animation: dash-md 2s ease infinite,rotate 2s linear infinite;
          animation: dash-md 2s ease infinite,rotate 2s linear infinite;
}
.loader-lg {
  stroke-width: 3;
  -webkit-animation: dash-lg 2s ease infinite,rotate 2s linear infinite;
          animation: dash-lg 2s ease infinite,rotate 2s linear infinite;
}

@-webkit-keyframes dash-lg {
  0% {
    stroke-dasharray: 1,157;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 130,157;
    stroke-dashoffset: -40;
  }
  100% {
    stroke-dasharray: 130,157;
    stroke-dashoffset: -157;
  }
}

@keyframes dash-lg {
  0% {
    stroke-dasharray: 1,157;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 130,157;
    stroke-dashoffset: -40;
  }
  100% {
    stroke-dasharray: 130,157;
    stroke-dashoffset: -157;
  }
}
@-webkit-keyframes dash-sm {
  0% {
    stroke-dasharray: 1,50;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 44,50;
    stroke-dashoffset: -13;
  }
  100% {
    stroke-dasharray: 44,50;
    stroke-dashoffset: -50;
  }
}
@keyframes dash-sm {
  0% {
    stroke-dasharray: 1,50;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 44,50;
    stroke-dashoffset: -13;
  }
  100% {
    stroke-dasharray: 44,50;
    stroke-dashoffset: -50;
  }
}
@-webkit-keyframes dash-md {
  0% {
    stroke-dasharray: 1,95;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 85,95;
    stroke-dashoffset: -25;
  }
  100% {
    stroke-dasharray: 85,95;
    stroke-dashoffset: -95;
  }
}
@keyframes dash-md {
  0% {
    stroke-dasharray: 1,95;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 85,95;
    stroke-dashoffset: -25;
  }
  100% {
    stroke-dasharray: 85,95;
    stroke-dashoffset: -95;
  }
}
@-webkit-keyframes rotate {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg); }
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg); }
}
@keyframes rotate {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg); }
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg); }
}



