.vehicles-page .table-row .table-cell {
  height: 70px;
  padding-top: 0;
  padding-bottom: 0;
}

.vehicles-page .vehicle-number {
  color: #2b3238;
  cursor: pointer;
}

.vehicles-page .vehicle-number:hover {
  color: #20c05c;
}

.vehicles-page .dropdown-menu {
  min-width: 100px;
}

.vehicles-page .emptyState img{
  min-height: 244px;
}

.vehicle-planner-page.listing-page .filters-wrapper .form-fields {
  margin: 0;
}

@media screen and (max-width: 480px) {
  .vehicle-planner-page .primary.button {
    overflow: hidden;
    width: 46px;
  }
  .vehicle-planner-page .filters-wrapper .primary, .vehicle-planner-page .filters-wrapper .button {
    min-width: 48%;
}
}



