.trip-planner-page-content .stats-section .refresh-button{
  width: 100%;
  margin-bottom: 15px;
}
.trip-planner-page-content .order-chart .order-chart-top{
  padding: 15px 15px 40px 15px;
  border: solid 1px #eaefef;
}
.trip-planner-page-content .order-chart-top .order-chart-heading-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}
.trip-planner-page-content .order-chart-top .order-heading {
  font-size: 1.2rem;
  font-weight: 600;
  color: #2b3238;
}
.trip-planner-page-content .order-chart-top select {
  width: 98px;
  padding: 0 10px;
  font-size: 0.8rem;
  font-weight: 600;
  color: #2b3238;
}
.trip-planner-page-content .order-chart-top .order-count {
  font-size: 12px;
  color: #80959d;
  font-weight: normal;
  margin-left: 0;
  margin-top: -7px;
}
.trip-planner-page-content .order-chart-bottom {
  padding: 15px;
  background-color: #e5f6ff;
}
.trip-planner-page-content .order-chart-bottom .stat-chart {
  margin-top: -45px;
}
.trip-planner-page-content .order-chart-bottom .stat-chart .heading {
  font-size: 0.8rem;
  color: #80959d;
  font-weight: 600;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}


