main.trips-details-page .top-heading {
    display: grid;
    grid-template-columns: 80% 20%;
}

main.trips-details-page .top-heading .dropdown-actions {
    text-align: right;

}

main.trips-details-page .main-content {
    display: grid;
    grid-template-columns: 300px calc(100% - 300px);
}

main.trips-details-page .main-content .map-with-path {
    border: 1px solid #d1d1d1;
    z-index: -10;
}

main.trips-details-page .main-content .map-with-path .address-map {
    height: 100%;
}

/* Agent-info container */
.trips-details-page .agent-info-container {
    border:1px solid  #eaefef;
    border-right: none;
    -webkit-box-shadow: 3px 1px 4px 0 rgba(0, 0, 0, 0.05);
            box-shadow: 3px 1px 4px 0 rgba(0, 0, 0, 0.05);
}

.trips-details-page .agent-info-container .agent-information .trip-closed {
    position: relative;
}
.trips-details-page .agent-info-container .agent-information .trip-closed img {
    position: absolute;
    left: 19px;
    z-index: -1;
}

.trips-details-page .agent-info-container .agent-information .trip-closed span {
    position: absolute;
    left: 34px;
    top:10px;
    font-size: 0.75rem;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.2px;
    color: #ffffff;
}

.trips-details-page {
    min-height: 302px;
}

.trips-details-page .agent-information {
    display:grid;
    grid-template-columns: 2fr 7fr 3fr;
    grid-gap:7px;
    margin:1.25rem 1.125rem;
}

.trips-details-page .agent-information .agent-pic img {
    height: 40px;
    width: 40px;
}

.trips-details-page .agent-information .agent-info span {
    display: block;
}

.trips-details-page .agent-information .agent-info span:first-child {
  font-size: 0.75rem;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #80959d;
}

.trips-details-page .agent-information .agent-info span:nth-child(2)
{
  font-size:0.875rem;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5;
  letter-spacing: -0.3px;
  color: #2b3238
}

.trips-details-page .agent-information .agent-info span:nth-child(3)
{
    font-size:0.75rem;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.1px;
    color: #2b3238;
}

.trips-details-page .vehicle-data {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: #fbfcfc;
}

.trips-details-page .vehicle-data > div {
    padding:0.5rem 0;
    border-top:1px solid #eaefef;
    border-bottom: 1px solid #eaefef;
    text-align:left;
    padding-left:1.25rem;
}

.trips-details-page .vehicle-data > div:first-child {
    border-right:1px solid #eaefef;
}

.trips-details-page .vehicle-data > div:nth-child(2) {
    text-align: center;
    padding-left: 0px;
}

.trips-details-page .vehicle-data > div:first-child span {
    font-size: 0.875rem;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.1px;
    color: #2b3238;
}

.trips-details-page .vehicle-data > div img {
    margin-right:15px;
}

.trips-details-page .vehicle-data > div:nth-child(2) span {
    font-size: 0.875rem;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.1px;
    color: #2b3238;
}

.trips-details-page .trip-timing-details, .trips-details-page .store-info {
    display: grid;
    grid-template-columns: 2fr 10fr;
    margin-top:2rem;

}
.trips-details-page .trip-timing-details .clock-asset {
    text-align: center;
    padding: .8em;
}

.trips-details-page .trip-timing-details .distance-time-info span {
    display: block;
}

.trips-details-page .trip-timing-details .distance-time-info span:nth-child(2n){
    font-size:0.875rem;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.1px;
    color: #2b3238;
    margin-bottom:1.5rem;
}

.trips-details-page .trip-timing-details .distance-time-info span:nth-child(2n+ 1){
    font-size:0.75rem;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.2px;
    color: #80959d;
}

.trips-details-page .store-info {
    border-top:1px solid #eaefef; 
    background:#fbfcfc;
    padding:1rem 0rem;
}

.trips-details-page .store-info span {
    font-size: 0.875rem;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.1px;
    color: #80959d;
}

.trips-details-page .store-info span:first-child {
    color:#2b3238;
    font-weight:700;
}

.trips-details-page .store-info .shop-icon-asset {
    display: inline-block;
    text-align: center;
}

.trip-details-order-list .table .table-row .table-cell:last-child > div{
    display: inline-block;
}   

.odometer-img .details {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

/* TripDetails OrderList Table */

.trips-details-page .trip-details-order-list {
    border:1px solid #eaefef;
    -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.05);
            box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.05);
    margin-top:2.5rem;
    padding:1.5625rem 1.875rem;
}

.trip-details-order-list > h3 {
    margin-top:0px;
}

.trip-details-order-list .order-list-elm-sn{
    display: inline-block;
    padding: .1rem .42rem;
    border:1px solid #eaefef;
    background:#fbfcfc;
}

.trip-details-order-list .order-list-elm-location .CustomerAddressWidget .address-icon {
    display: inline-block;
    width:0.875rem;
    height:1.5rem;
    background-position: center;
    cursor: pointer;
    background-image: url('./address-inactive.svg');
    background-repeat: no-repeat;
}

/* Dashed Lines CSS */
.trips-details-page .agent-info-container .trip-timing-details .distance-time-info {
    position: relative;
}

.trips-details-page .agent-info-container .trip-timing-details .clock-asset {
    position: relative;
}

.trips-details-page .agent-info-container .trip-timing-details .distance-time-info:not(:last-child)::after {
    content: '';
    position: absolute;
    height: 1.625rem;
    left: -10%;
    bottom: 0px;
    border-left:1px dashed #80959d;
}

.trips-details-page .agent-info-container .trip-timing-details .clock-asset:not(:first-child)::before {
    content: '';
    position: absolute;
    height: 1.1875rem;
    left: 50%;
    top:0.125rem;
    border-left:1px dashed #80959d;
}
/* Dashed Lines in Listing Page */
.trips-details-page .table .table-row .table-cell .completed-at{
    position: relative;
    display: inline-block;
    margin-right: 1rem;
    letter-spacing: -0.2px;
    padding-right: .7rem;
}

.trips-details-page .trip-details-order-list .table .table-row .table-cell:first-child {
    background: white;
    border:none;
    padding: 0px;
    width: 100px;
}

.trip-details-order-list .table-row .table-cell:first-child {
    position: relative;
}

.trip-details-order-list .table-row:not(:last-child) .table-cell:first-child::after {
    content: '';
    height: 50%;
    top: 50%;
    right: 25%;
    position: absolute;
    border-left: 1px solid #eaefef;
    width: 1%;
}

.trip-details-order-list .table-row:not(:nth-child(2)) .table-cell:first-child::before {
    content: '';
    height: 50%;
    top:0px;
    right: 25%;
    position: absolute;
    border-left: 1px solid #eaefef;
    width:1%;
}

.trip-details-order-list .table-row .table-cell .asset {
    position: absolute;
    top:50%;
    width: 0.3125rem;
    height: 0.3125rem;
    border-radius: 100%;
    background:#80959d;
    right: 23.5%;
    z-index: 100;
}

.trip-details-order-list .table-row .table-cell span.asset.COMPLETED {
    background:#20c05c;
}

.trip-details-order-list .table-row .table-cell span.asset.CANCELLED {
    background:#ec8383;
}


.trip-details-order-list .eta {
    font-size:	0.75rem;
    margin-top: 1px;
}

.trip-details-order-list .eta.CANCELLED.text-muted {
    display: none;
}

.trip-details-order-list .eta .negativeTime {
    color:red;
    font-weight: 600;
    font-size: 12px;
}

.trip-details-order-list .eta .positiveTime {
    color:#20c05c;
    font-size: 0.75rem;
}   
.trips-details-order-list .eta.COMPLETED.text-muted {
    margin-top: 1px;
}

.trip-details-order-list .signature {
    margin-left: 5px;
    cursor: pointer;
}

/* Media Queries */
@media only screen and (max-width: 650px) {
    main.trips-details-page .main-content {
        display: grid;
        grid-template-columns: 50% 50%;
    }
    .trips-details-order-list .table .table-row .table-cell,.trips-details-order-list .table .table-row .table-cell small, .trips-details-order-list .table .table-row .table-cell,.trips-details-order-list .table .table-row .table-cell div{
        font-size: 0.75rem;
    }
    .trips-details-page .trip-details-order-list .table .table-header .table-cell {
        vertical-align: top;
    }
    main.trips-details-page .trip-details-order-list .table-container{
        overflow-x: auto;
    }
    .trip-details-order-list .table-row .table-cell .asset {
        right: 22.5%;
    }
}

@media only screen and (max-width: 540px) {
    main.trips-details-page .main-content {
        display: grid;
        grid-template-columns: 1fr;
    }
    main.trips-details-page .main-content .map-with-path {
        height: 25rem;
    }
    .trips-details-page .agent-info-container {
        border-right: 1px solid #eaefef;
    }

    .trip-details-order-list .table-row:not(:last-child) .table-cell:first-child::after {
        right:26%;
    }

    .trip-details-order-list .table-row:not(:nth-child(2)) .table-cell:first-child::before {
        right:26%;
    }
    .trips-details-page .agent-info-container .agent-information .trip-closed {
        margin-left: 5px;
    }
}

@media only screen and (min-width: 1250px) {
    
    .trip-details-order-list .table-row .table-cell .asset {
        right: 24.5%;
    }

    .trip-details-order-list .table-row:not(:last-child) .table-cell:first-child::after {
        right:26%;
    }

    .trip-details-order-list .table-row:not(:nth-child(2)) .table-cell:first-child::before {
        right:26%;
    }
}

@media screen and (max-width: 480px) {
    .trip-details-order-list .table .table-row {
        display: grid;
        border-left: 1px solid #eaefef;
        margin-left: 66px;
        position: relative;
        background-color: white;
    }
       .trip-details-order-list .table .table-row .table-cell{
        border: none;
        margin-left: 16px;
    }
    
    .trip-details-order-list .table .table-row .table-cell small.text-muted{
        color: #2b3238;
    }
    .trip-details-order-list .table .table-row .table-cell small.text-muted::before, .trip-details-order-list .table .table-row .table-cell .order-status{
        content: ',';
        margin-right: 5px;
    }
    .trip-details-order-list .table .table-row .table-cell .eta.text-muted {
        color: #2b3238;        
    }
    .trip-details-order-list .table .table-cell:last-child {
        text-align: left;
    }
    .trip-details-order-list .table .table-row .table-cell div {
        display: inline-block;
    }
    .trip-details-order-list .table .table-row .table-cell:last-child div.text-muted1 {
        display: block;
    }
    .trip-details-order-list .table .table-row .table-cell .text-muted1{
        color: #80959d;
        display: block;
    }
       .trip-details-order-list .table-header {
        display: none;
    }
    .trip-details-order-list .table .table-cell:first-child {
        position: absolute;
        left: -90px;
    }
    .trip-details-order-list .table-row .table-cell .asset {
        right: 0px;
        left: 71px;
    }
    .trip-details-order-list .table-row .table-cell:last-child {
        margin-right: 12px;
        border-bottom: 1px solid #eaefef;
    }
    .trips-details-order-list .table-row .table-cell.trip-order-sno {
        margin-top: -8px;
    }
}



