.campaigns-page .campaign-title-link {
    color: #2b3238;
}

.campaigns-page .campaign-title-link:hover {
    color: #20c05c;
}

.campaigns-page .dropdown-menu {
    width : 139px;
}

.campaigns-page .campaign-mobile-view {
    display: none;
}

@media screen and (max-width: 480px) {
    .campaigns-page .table-container {
        margin: 0px;
    }
      .campaigns-page .table .table-header {
        display: none;
      }
      .campaigns-page .table .table-row {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        position: relative;
        border:1px solid #eaefef;
        background: #ffffff;
        margin:15px 0;
      }
      .campaigns-page .table .table-row .table-cell{
          border: 0px;
          padding: 15px;
      }
      .campaigns-page .campaign-mobile-view {
          display: block;
          color: #80959d;
          font-size: 11px;
          font-weight: normal;
      }
      .campaigns-page .campaign-name {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 1;
        min-width: 290px;
      }
      .campaigns-page .campaign-status {
        grid-column-start: 1;
        grid-row-start: 2;
        font-weight: 600;
      }
    
    .campaigns-page .campaign-date {
        grid-column-start: 2;
        grid-column-end: 4;
        grid-row-start: 2;
    }
    .campaigns-page .campaign-actions {
        grid-column-start: 3;
        grid-row-start: 1;
    }
    .campaigns-page .table-row .campaign-title-link {
        font-weight: 600;
        font-size: 14px;
    }
    .campaigns-page .table .table-row .table-cell.campaign-date, .campaigns-page .table .table-row .table-cell.campaign-status{
        background-color: #fbfcfc;
        border-top: 1px solid #eaefef;
        font-size: 13px;
    }
    .campaigns-page .table .table-row .table-cell.campaign-date .scheduled-date,.campaigns-page .table .table-row .table-cell.campaign-date .scheduled-time {
        font-size: 13px;
        color: #2b3238;
        font-weight: 600;
    }
    .campaigns-page .table .table-row .table-cell.campaign-date div:nth-child(2) {
        display: -ms-flexbox;
        display: flex;
    }
    .campaigns-page .table .table-row .table-cell.campaign-date .scheduled-date::after {
        content: ',';
        margin-right: 5px;
    }
}

@media screen and (max-width: 360px) {
    .campaigns-page .campaign-name {
        min-width: auto;
    }
}


