.VehiclePlannerForm .field {
  margin-bottom: 20px;
}

.VehiclePlannerForm .form-actions {
  text-align: right;
}

.VehiclePlannerForm input[type='submit'],
.VehiclePlannerForm .button {
  min-width: 110px;
  width: auto;
}

.VehiclePlannerForm .button:not(:last-child) {
  margin-right: 10px;
}
  
.VehiclePlannerForm .availability-status .inline-label {
  line-height: 44px;
}

.VehiclePlannerForm .vehicle-form-fields {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
}

@media screen and (min-width:510px) {
  .VehiclePlannerForm .field-group {
    display: -ms-flexbox;
    display: flex;
    margin: 0 -10px;
  }

  .VehiclePlannerForm .field-group > .field {
    /* width: 50%; */
    padding: 0 10px;
  }
  
}

@media screen and (max-width: 480px) {
  .VehiclePlannerForm .vehicle-form-fields {
    display: block;
  }
  .VehiclePlannerForm .vehicle-form-fields .form-actions .primary{
    width: 48%;
  }
  .SingleDatePicker.SingleDatePicker_1 .DayPicker_transitionContainer.DayPicker_transitionContainer_1.DayPicker_transitionContainer__vertical.DayPicker_transitionContainer__vertical_2 {
    height: 330px !important;
  }
}


