.VehicleForm .field {
  margin-bottom: 20px;
}

.VehicleForm .form-actions {
  text-align: right;
}

.VehicleForm input[type='submit'],
.VehicleForm .button {
  min-width: 110px;
  width: auto;
}

.VehicleForm .button:not(:last-child) {
  margin-right: 10px;
}
  
.VehicleForm .availability-status .inline-label {
  line-height: 44px;
}

.VehicleForm .Radio .radio-label div{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.VehicleForm .form-actions {
  -ms-flex-pack: end;
      justify-content: end;
}

@media screen and (min-width:510px) {
  .VehicleForm .field-group {
    display: -ms-flexbox;
    display: flex;
    margin: 0 -10px;
  }

  .VehicleForm .field-group > .field {
    /* width: 50%; */
    padding: 0 10px;
  }

  .VehicleForm .vehicle-type {
    width: 66%;
  }

  .VehicleForm .availability-status {
    width: 34%;
  }

  .VehicleForm .radio-label-icon {
    margin-left: 12px;
  }
  
}

@media screen and (max-width:480px) {
  .VehicleForm input[type='submit'], .VehicleForm .button{
    min-width: 48%;
  }
  .VehicleForm .form-actions {
    margin-top: -14px;
  }

  .VehicleForm .vehicle-type .radio-label-text img{
    margin-left: 5px;
  }
}



