.slot-store-page form {
    max-width: 100%;
}
.slot-store-page form .table-container {
    display: grid;
    grid-template-columns: 180px 1fr;
}
.slot-store-page form .store-slot-rules {
    display: -ms-flexbox;
    display: flex;
    overflow-x: auto;
}
.slot-store-page .success {
    font-size: 0.875rem;
    color: #20c05c;
    margin-bottom: 20px;
}
  
.slot-store-page .store-slot-container div {
    -ms-flex-align: center;
        align-items: center;
    padding: 12px;
    font-size: 0.875rem;
}

.slot-store-page .store-slot-container .slot-date {
    background-color: #fcfcfc;
    font-weight: 600;
    border-bottom: solid 1px #ccc;
    color: #80959d;
    height: 38px;
    font-size: 0.75rem;
}

.slot-store-page .store-slot-container {
    min-width: -webkit-max-content;
    min-width: -moz-max-content;
    min-width: max-content;
    text-align: center;
}
.slot-store-page .slot-table-header {
    font-size: 0.875rem;
    font-weight: 600;
    background-color: #fcfcfc;
    text-align: center;
}

.slot-store-page .response-status {
    margin-top: 20px;
}

.slot-store-page .slot-table-header > div {
    padding: 12px;
}

.slot-store-page .store-slot-container .slot-config .slot-time {
    font-size: 0.875rem;
}

.slot-store-page form .slot-name-header {
    border-bottom: solid 1px #ccc;
    color: #80959d;
    height: 38px;
    font-size: 0.75rem;
    font-weight: 600;
}

.slot-store-page form .orderType {
    max-width: 8rem;
    margin-bottom: 1rem;
}

.slot-store-page .no-slots {
    min-height: 300px;
}

.slot-store-page .store-slot-container .slot-date, .slot-store-page .slot-table-header .slot-name-header {
    background-color: white;
}

.slot-store-page form .slot-name-header, .slot-store-page .store-slot-container .slot-date {
    border-bottom: 1px solid #eaefef;
}
@media(max-width:480px){
    .slot-store-page input[type="submit"], .slot-store-page .button {
        width:48%;
    }
    .slot-store-page h1 {
        line-height: 1.5;
        font-size: 1.25rem; 
        margin-bottom: 30px;
    }
    .slot-store-page form .orderType {
        max-width: 100%;
    }
    .slot-store-page .slot-table-header .slot-name, .slot-store-page .slot-table-header .slot-name-header{
        text-align: left;
    }
}


