  max-width: 1280px;
}
.trip-sequence-popup .details {
  padding: 0;
}
.TripSequenceMap .address-map {
  height: 100%;
}
.TripSequenceMap .trip-sequence-details-heading {
  padding: 20px 30px;
  color: #ffffff;
  background-color: #80959d;
  font-size: 1.2rem;
  font-weight: bold;
}
.TripSequenceMap .trip-sequence-orders {
  padding: 30px;
  height: 250px;
  overflow-y: auto;
}
.TripSequenceMap .trip-sequence-orders > div > h3 {
  margin-top: 0;
  font-size: 0.875rem;
  color: #2b3238;
}
.trip-sequence-popup .trip-orders-inner-container {
  min-height: 32px;
}
.trip-sequence-popup .order-box-spacer {
  padding: 6px 20px;
  min-height: 32px;
  background-color: #ffffff;
  border: 1px solid #eaefef;
}
