  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.details img {
  max-height: unset;
}

.trip-sequence-popup .no-store-location {
  min-height: 100px;
  padding: 40px 20px 20px;
  color: #f50c0c;
}


