.vehicles-page .table-row .table-cell {
  height: 70px;
  padding-top: 0;
  padding-bottom: 0;
}

.vehicles-page .vehicle-number {
  color: #2b3238;
  cursor: pointer;
}

.vehicles-page .vehicle-number:hover {
  color: #20c05c;
}

.vehicles-page .dropdown-menu {
  min-width: 100px;
}

.vehicles-page .emptyState img{
  min-height: 244px;
}

.vehicles-page .vehicle-icon {
  width: 30px;
  margin-right: 5px;
  margin-top: -4px;
}

.vehicles-page .vendor-btn {
  margin-right: 10px;
}

