    left:67px;
    top: 36px;
    font-size: 12px;
  }
  .vehicles-page .table-cell.vehicle-vendor .text-muted {
    font-size: 11px;
  }

  .vehicles-page .table-cell.vehicle-vendor .text-muted .vehicle-text-muted{
    font-size: 13px;
  }
  .vehicles-page .DropDown .dropdown-menu .dropdown-items .dropdown-item {
    font-size: .8125rem;
  }
  .vehicles-page .vehicle-vendor
  {
    position: absolute;
    width:100%;
