  .vehicles-page .header-container .header-actions-wrapper .search-button-wrapper {
    position: absolute;
    top: -2px;
    right: -10px;
  } 
  .vehicles-page .header-container .header-actions-wrapper {
    margin-left: -94px;
  }
  .vehicles-page .primary.button.vendor-btn {
    width: 94%;
    font-size: 14px;
  }
  .vehicles-page .header-actions-wrapper .button {
    width: 45%;
    font-size: 14px;
  }  
  .vehicles-page .filters-wrapper .primary, .vehicles-page .filters-wrapper button.button {
    width: 48%;
  }
  .vehicles-page .filters-wrapper .form-fields .Radio .radio-label {
    min-width: 33%;
  }
  .vehicles-page .table .table-cell.vehicle-vendor {
    border-top: 1px solid #eaefef;
  }
  .vehicles-page .table div.table-cell div.vehicle-number {
    font-size: 14px;
  }
}

@media screen and (max-width:360px){
  .vehicles-page .header-actions-wrapper .button {
    padding: 0 5px;
  }
  .vehicles-page .header-container .header-actions-wrapper {
    margin-left: -112px;
}
}


