.trips-page .table-row > .table-cell {
  height: 70px;
  padding-top: 0;
  padding-bottom: 0;
}

.trips-page .trip-details {
  color: #2b3238;
}

.trips-page .dropdown-menu {
  min-width: 168px;
}

.trips-page .vehicle-number {
  color: #80959d;
  font-size: 0.875rem;
  line-height: 24px;
}

.trips-page .vehicle-number.type-2W::before,
.trips-page .vehicle-number.type-4W::before {
  content: '';
  display: inline-block;
  width: 24px;
  height: 24px;
  vertical-align: bottom;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 4px;
}

.trips-page .vehicle-number.type-2W::before {
  background-image: url('./2-wheeler.svg');
}

.trips-page .vehicle-number.type-4W::before {
  background-image: url('./4-wheeler.svg');
}

.trips-page .editPopup {
  max-width: 560px;
  text-align: initial;
}

.trips-page .editPopup input[type=submit] {
  width: auto;
  margin-left: 12px;
}

.trip-details-popup {
  max-width: 800px;
}

.trips-page .filters-wrapper .status-filter {
  width: 100%;
}

.trips-page .filters-wrapper .radio-label {
  -ms-flex: initial;
      flex: initial;
  padding: 0 16px;
}

.trip-finclose-form {
  max-width: 1000px;
}

.trip-finclose-form form {
  max-width: 100%;
}

.trips-page .trip-id a {
  color: #2b3238;
  cursor: pointer;
}

.trips-page .trip-id a:hover {
  color: #20c05c;
}

@media (max-width: 480px){
  .trips-page .table .table-header{
    display:none;
  }
  .trips-page .table-container{
    border-top: 1px solid #eaefef;
    margin: 0px -25px;
    padding: 15px 20px;
  }
  .trips-page .table .table-row {
    display: block;
    margin-bottom: 15px;
    border: 1px solid#eaefef;
    min-height: 115px; 
    position: relative;
  }
  .trips-page .table .table-row .table-cell div {
    font-size: 13px;
    font-weight: 600;
  }
  .trips-page .table .table-row div.trip-details.trips-agent-name {
    font-weight: normal;
  }
  .trips-page .table .table-row:nth-child(2n){
    background-color: #fff;
  }
  .trips-page .table .table-row .table-cell{
    border:none;
  }
  .trips-page .table .table-cell:first-child {
    padding-left: 10px;
  }
  .trips-page .table .table-row .trip-start-time{
    position: absolute;
    font-size: 12px;
    width:100%;
    background: #fbfcfc;
    border-top: 1px solid #eaefef;
  }
  .trips-page .table .trip-end-time{
    left:173px;
    position: absolute;
    font-size: 12px;
  }
  .trips-page .table .trip-end-time .trip-header-endtime{
    color:#80959d;
  }
  .trips-page .table .trip-start-time .trip-header-starttime{
    color:#80959d;
  }
  .trips-page .table .trip-details .trips-vehicle-number{
    display:none;
  }
  .trips-page .table .vehicle-number{
    border:1px solid #dadee0;
    position:absolute;
    top:10px;
  }
  .trips-page .table .table-row .table-cell.trip-id{
    position: absolute;
    left:53px;
    top:-11px;
    font-size:14px;
  }
  .trips-page .table .trips-agent-name{
    position: absolute;
    left: 62px;
    top: 30px;
  }
  .trips-page .vehicle-number.type-2W::before, .trips-page .vehicle-number.type-4W::before{
    width:40px;
    height:40px;
  }
  .trips-page .table .trip-status{
    position: absolute;
    right:38px;
    top:-11px;
  }
  .trips-page .table .trip-actions{
    position: absolute;
    top: -10px;
    right: 6px
  }
  .trips-page .DropDown .dropdown-toggle{
    padding: 0px;
  }
  .trips-page .table .trip-start-time .trip-header-time{
    padding-left: 5px;
  }
  .trips-page .table .trip-end-time .trip-header-time{
    padding-left: 5px;
  }
  .trips-page .table-row > .table-cell {
    height: 51px;
    padding-top: 10px;
    margin-top: 10px;  
  }
.trips-page .filters-wrapper .radio-label {
  -ms-flex: 1 1;
      flex: 1 1;
  z-index: 0;
}
.trips-page .filters-wrapper .primary, .trips-page .filters-wrapper .button {
  width: 48%;
}
.trips-page .table .table-cell.trip-id .trip-details a{
  font-size: 14px;
  font-weight: 600;
}
.trips-page .table .table-row .table-cell .trip-header-starttime, .trips-page .table .table-row .table-cell .trip-header-endtime {
  font-size: 11px;
}
.trips-page .table .table-row .table-cell .DropDown .dropdown-items .dropdown-item {
  font-size: 13px;
}
}



