.picking-queue-page .table .table-cell:last-child {
    text-align: center;
}

@media (max-width: 480px) {
    .picking-queue-page .table-container {
        margin: 0px -25px;
        padding: 0px 25px;
        border-top: 1px solid #eaefef;  
    }
    .picking-queue-page .table .table-header {
        display: none;
      }
    .picking-queue-page .table .table-row {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        position: relative;
        border:1px solid #eaefef;
        background: #ffffff;
        margin:20px 0;
      }
    .picking-queue-page .table .table-row .table-cell{
        border: none;
        padding: 15px;
    }
    .picking-queue-page .table .table-cell .mobile-header {
        color: #80959d;
        font-size: 11px;
        margin-bottom: 5px;
    }
    .picking-queue-page .table .table-cell .order-number {
        font-size: 14px;
        font-weight: 600;
    }
    .picking-queue-page .table .table-row div.table-cell small.text-muted, .picking-queue-page .table .table-cell div{
        color: #2b3238;
        font-size: 13px;
    }
    .picking-queue-page .table .table-row .table-cell.order-preffered-date {
        grid-column-start:2;
        grid-column-end:4;
        grid-row-start:1; 
    }
    .picking-queue-page .table .table-row .table-cell.order-place-time, .picking-queue-page .table .table-row .table-cell.column-order-amount, .picking-queue-page .table .table-row .table-cell.column-order-status {
        grid-row-start:2;  
        border-top: 1px solid #eaefef;
        background: #fbfcfc;
        font-weight: 600; 
    }
    .picking-queue-page .table .table-row .table-cell.column-order-amount {
        text-align: center;
    }
    .picking-queue-page .table .table-row .table-cell.column-order-status {
        text-align: right;
    }
    .picking-queue-page .table .table-row .order-preffered-date div:nth-child(2) {
        display: inline;
    }
    .picking-queue-page .table .table-row .order-preffered-date div:nth-child(2)::after {
        content: ',';
        margin-right: 5px;
    }
}


